import { getTasks } from '@/app/actions/task'

export default async function TasksPage() {
  const tasks = await getTasks()

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold mb-4">Tasks Backend Test</h1>
      <p className="mb-4">Nombre de tâches: {tasks.length}</p>

      <pre className="bg-gray-100 p-4 rounded overflow-auto">
        {JSON.stringify(tasks, null, 2)}
      </pre>

      <div className="mt-4 p-4 bg-green-100 border border-green-400 rounded">
        <p className="text-green-800 font-semibold">
          ✅ Backend fonctionnel! Killian peut maintenant créer l'interface.
        </p>
      </div>
    </div>
  )
}
